<template>
<div  id="mainView">
  <div class="container" v-if="nowKey">
    <div class="jumbotron p-4 p-md-5 text-white rounded bg-dark" style="background-repeat:no-repeat;background-size: cover;" :style="{backgroundImage: 'url(' + locateImg[nowKey] + ')'  }">
      <div class="col-md-6 px-0">
        <h1 class="display-4 font-italic"><b>{{locate[nowKey]}}</b></h1>
      </div>
    </div>
    <div class="row mb-2">
      <div class="col-md-6" v-for="(val,key) in hotelArr[nowKey].data">
        <div class="row no-gutters border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative">
          <div class="col p-4 d-flex flex-column position-static">
            <strong class="d-inline-block mb-2" v-bind:class="{'text-primary':key%2 ==0, 'text-success':key%2 == 1,}">旅館</strong>
            <h3 class="mb-0">{{val['觀光旅館名稱']}}</h3>
            <div class="mb-1 text-muted">{{val['地址']}}</div>
            <!-- <p class="card-text mb-auto" v-if="val['經度'] && val['緯度']">經度:{{val['經度']}},緯度:{{val['緯度']}}</p> -->
          </div>
          <div class="col-auto d-none d-lg-block">
            <!-- <svg class="bd-placeholder-img" width="200" height="250" xmlns="https://img.ltn.com.tw/Upload/liveNews/BigPic/600_3042788_2_1.jpg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img" aria-label="Placeholder: Thumbnail"><title>Placeholder</title><rect width="100%" height="100%" fill="#55595c"/><text x="50%" y="50%" fill="#eceeef" dy=".3em">{{val['觀光旅館名稱']}}</text></svg> -->
            <iframe width="250" height="250" frameborder="0" v-if="val['經度'] && val['緯度']" :src="'https://www.google.com/maps?q='+ val['緯度'] + ',' + val['經度']  +'&hl=zh-TW&output=embed'"></iframe>
            <iframe width="250" height="250" frameborder="0" v-if="!val['經度'] && !val['緯度']" :src="'https://www.google.com/maps?q='+ val['地址'] +'&hl=zh-TW&output=embed'"></iframe>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>

</div>
</template>
<script>
export default {
  name: 'mainView',
  props : ['nowKey'],
  data () {
    return {
      locate: {"0":"高雄市","1":"桃園市","2":"臺中市"},
      locateImg: {"0":"data:image/jpeg;base64,",
      "1":"https://image.cache.storm.mg/styles/smg-800x533-fp/s3/media/image/2018/10/08/20181008-054948_U13613_M460807_031d.jpg?itok=AvClS8il",
      "2":"https://img.ltn.com.tw/Upload/liveNews/BigPic/600_3042788_2_1.jpg"},
      // nowKey: '0',
      hotelArr: [],
    }
  },
  methods: {
    getData() {
        const cors = 'https://cors-anywhere.herokuapp.com/';
        this.$axios.get('https://api.kcg.gov.tw/api/service/Get/cb50902c-3681-43ec-89da-705f68eafb88').then(result => {
            this.hotelArr[0] = result.data;
        }) .catch(error => {
            console.log(error);
        });
        this.$axios.get(cors + 'https://data.tycg.gov.tw/api/v1/rest/datastore/c3340a19-9219-498a-9a46-21de506ba85b?format=json').then(result => {
            // this.hotelArr.push(result.data.);
            var temp = [];
            temp['isImage'] = false;
            var data = [];
            var i = 1;
            result.data.result.records.forEach(function(item){
                  var tempData = [];
                  tempData['seq'] = i;
                  tempData['觀光旅館名稱'] = item.Name;
                  tempData['地址'] = item.Add;
                  tempData['經度'] = item.Px;
                  tempData['緯度'] = item.Py;
                  data.push(tempData);
            });
            temp['data'] = data;
            this.hotelArr[1] = temp;
            // this.hotelArr.push(temp);
        }) .catch(error => {
            console.log(error);
        });
        
        
        this.$axios.get('https://datacenter.taichung.gov.tw/Swagger/OpenData/2e69d77f-debc-4f58-8f5f-2d21479472bc').then(result => {
            var temp = [];
            temp['isImage'] = false;
            var data = [];
            var i = 1;
            result.data.forEach(function(item){
                  var tempData = [];
                  tempData['seq'] = item['編號'];
                  tempData['觀光旅館名稱'] = item['旅宿名稱'];
                  tempData['地址'] = item['地址'];
                  // tempData['經度'] = item.Px;
                  // tempData['緯度'] = item.Py;
                  data.push(tempData);
            });
            temp['data'] = data;
            this.hotelArr[2] = temp;
        }) .catch(error => {
            console.log(error);
        });
        
    }, 
    setTaichung(data) {
      this.hotelArr[2] = data;
    }
    ,
    getLocaInfo(inx) {
      // console.log(inx);
      // this.nowKey = inx;
      // console.log(inx);
      // console.log(JSON.stringify(this.hotelArr[inx]));
    }
  },
    mounted() {
        this.getData();
    }
}
</script>


