<template>
  <div id="app">
      <div class="container">
        <header class="blog-header py-3">
          <div class="row flex-nowrap justify-content-between align-items-center">
            <div class="col-4 pt-1">
              <a class="text-muted" href="#">Subscribe</a>
            </div>
            <div class="col-4 text-center">
              <a class="blog-header-logo text-dark" href="#">HowHow丸旅遊網</a>
            </div>
            <div class="col-4 d-flex justify-content-end align-items-center">
              <a class="text-muted" href="#" aria-label="Search">
                <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" class="mx-3" role="img" viewBox="0 0 24 24" focusable="false"><title>Search</title><circle cx="10.5" cy="10.5" r="7.5"/><path d="M21 21l-5.2-5.2"/></svg>
              </a>
              <a class="btn btn-sm btn-outline-secondary" href="#">註冊</a>
            </div>
          </div>
        </header>

        <div class="nav-scroller py-1 mb-2">
          <nav class="nav d-flex justify-content-between">
            <!-- <a href="#" >{{item}}</a> -->
            <button type="button" class="btn btn-outline-primary" v-for="(item, id) in locate" @click="setWeather(item, id)">{{item}}</button>
          </nav>
        </div>

        <!-- <div class="jumbotron p-4 p-md-5 text-white rounded bg-dark">
          <div class="col-md-6 px-0">
            <h1 class="display-4 font-italic">Title of a longer featured blog post</h1>
            <p class="lead my-3">Multiple lines of text that form the lede, informing new readers quickly and efficiently about what’s most interesting in this post’s contents.</p>
            <p class="lead mb-0"><a href="#" class="text-white font-weight-bold">Continue reading...</a></p>
          </div>
        </div> -->
        <div id="myCarousel" class="carousel slide" data-ride="carousel">
          <!-- <ol class="carousel-indicators">
            <li v-for="(item, id) in locationData" :data-slide-to="id" v-bind:class="{ 'active': id == 0}"></li>
          </ol> -->
          <div class="carousel-inner" >
            <div class="carousel-item" v-for="(item, id) in locationData" v-bind:class="{ 'active': id == 0}">
              <svg class="bd-placeholder-img" width="100%" height="100%" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img"><rect width="100%" height="100%" fill="#777"/></svg>
              <div class="container">
                <div class="carousel-caption text-left">
                  <h1>{{item.elementName}}</h1>
                  <p v-for="(item2, id2) in item.time" v-if="id2 < 3">
                    {{item2.startTime}}~{{item2.endTime}} {{item2.parameter.parameterName}} {{item2.parameter.parameterValue}}
                  </p>
                </div>
              </div>
            </div>
          </div>
          <a class="carousel-control-prev" href="#myCarousel" role="button" data-slide="prev">
            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
            <span class="sr-only">Previous</span>
          </a>
          <a class="carousel-control-next" href="#myCarousel" role="button" data-slide="next">
            <span class="carousel-control-next-icon" aria-hidden="true"></span>
            <span class="sr-only">Next</span>
          </a>
        </div>
        <h2 v-if="nowloc">{{nowloc}} 未來36小時天氣</h2>
        <div class="row mb-2">
          <div class="col-md-6" v-for="(item, id) in locationData">
            <div class="row no-gutters border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative">
              <div class="col p-4 d-flex flex-column position-static">
                <strong class="d-inline-block mb-2 text-primary">{{id.split('~')[0]}}<br>{{id.split('~')[1]}}</strong>
                <!-- <div class="mb-1 text-muted" v-for="(item2, id2) in item.time">{{item2.startTime}}<br>{{item2.endTime}}</div> -->
                <p class="card-text mb-auto"><svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-cloud" fill="currentColor" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M4.406 3.342A5.53 5.53 0 0 1 8 2c2.69 0 4.923 2 5.166 4.579C14.758 6.804 16 8.137 16 9.773 16 11.569 14.502 13 12.687 13H3.781C1.708 13 0 11.366 0 9.318c0-1.763 1.266-3.223 2.942-3.593.143-.863.698-1.723 1.464-2.383zm.653.757c-.757.653-1.153 1.44-1.153 2.056v.448l-.445.049C2.064 6.805 1 7.952 1 9.318 1 10.785 2.23 12 3.781 12h8.906C13.98 12 15 10.988 15 9.773c0-1.216-1.02-2.228-2.313-2.228h-.5v-.5C12.188 4.825 10.328 3 8 3a4.53 4.53 0 0 0-2.941 1.1z"/></svg>：{{item.Wx.parameterName}}</p>
                <p class="card-text mb-auto"><svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-emoji-sunglasses" fill="currentColor" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M8 15A7 7 0 1 0 8 1a7 7 0 0 0 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/><path fill-rule="evenodd" d="M4.285 9.567a.5.5 0 0 1 .683.183A3.498 3.498 0 0 0 8 11.5a3.498 3.498 0 0 0 3.032-1.75.5.5 0 1 1 .866.5A4.498 4.498 0 0 1 8 12.5a4.498 4.498 0 0 1-3.898-2.25.5.5 0 0 1 .183-.683zM6.5 6.497V6.5h-1c0-.568.447-.947.862-1.154C6.807 5.123 7.387 5 8 5s1.193.123 1.638.346c.415.207.862.586.862 1.154h-1v-.003l-.003-.01a.213.213 0 0 0-.036-.053.86.86 0 0 0-.27-.194C8.91 6.1 8.49 6 8 6c-.491 0-.912.1-1.19.24a.86.86 0 0 0-.271.194.213.213 0 0 0-.036.054l-.003.01z"/><path d="M2.31 5.243A1 1 0 0 1 3.28 4H6a1 1 0 0 1 1 1v1a2 2 0 0 1-2 2h-.438a2 2 0 0 1-1.94-1.515L2.31 5.243zM9 5a1 1 0 0 1 1-1h2.72a1 1 0 0 1 .97 1.243l-.311 1.242A2 2 0 0 1 11.439 8H11a2 2 0 0 1-2-2V5z"/></svg>：{{item.CI.parameterName}}</p>
                <p class="card-text mb-auto"><svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-droplet-half" fill="currentColor" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M7.21.8C7.69.295 8 0 8 0c.109.363.234.708.371 1.038.812 1.946 2.073 3.35 3.197 4.6C12.878 7.096 14 8.345 14 10a6 6 0 0 1-12 0C2 6.668 5.58 2.517 7.21.8zm.413 1.021A31.25 31.25 0 0 0 5.794 3.99c-.726.95-1.436 2.008-1.96 3.07C3.304 8.133 3 9.138 3 10c0 0 2.5 1.5 5 .5s5-.5 5-.5c0-1.201-.796-2.157-2.181-3.7l-.03-.032C9.75 5.11 8.5 3.72 7.623 1.82z"/><path fill-rule="evenodd" d="M4.553 7.776c.82-1.641 1.717-2.753 2.093-3.13l.708.708c-.29.29-1.128 1.311-1.907 2.87l-.894-.448z"/></svg>：{{item.PoP.parameterName}}%</p>
                <p class="card-text mb-auto"><svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-brightness-high" fill="currentColor" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M8 11a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm0 1a4 4 0 1 0 0-8 4 4 0 0 0 0 8zM8 0a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-1 0v-2A.5.5 0 0 1 8 0zm0 13a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-1 0v-2A.5.5 0 0 1 8 13zm8-5a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1 0-1h2a.5.5 0 0 1 .5.5zM3 8a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1 0-1h2A.5.5 0 0 1 3 8zm10.657-5.657a.5.5 0 0 1 0 .707l-1.414 1.415a.5.5 0 1 1-.707-.708l1.414-1.414a.5.5 0 0 1 .707 0zm-9.193 9.193a.5.5 0 0 1 0 .707L3.05 13.657a.5.5 0 0 1-.707-.707l1.414-1.414a.5.5 0 0 1 .707 0zm9.193 2.121a.5.5 0 0 1-.707 0l-1.414-1.414a.5.5 0 0 1 .707-.707l1.414 1.414a.5.5 0 0 1 0 .707zM4.464 4.465a.5.5 0 0 1-.707 0L2.343 3.05a.5.5 0 1 1 .707-.707l1.414 1.414a.5.5 0 0 1 0 .708z"/></svg>：最低 {{item.MinT.parameterName}}°C，最高 {{item.MaxT.parameterName}}°C</p>
                <!-- <p class="card-text mb-auto">{{item}}</p> -->
              </div>
              <div class="col-auto d-none d-lg-block">
                <img width="200" height="200" :src="'https://www.cwb.gov.tw/V8/assets/img/weather_icons/weathers/svg_icon/day/'+ item.Wx.parameterValue.padStart(2, '0') +'.svg'"></img>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- <div v-for="(item, id) in weather">
        {{item.locationName}} <br>
      </div> -->
  <mainview :nowKey="idkey"></mainview>
  </div>
</template>

<script>
import mainview from '@/components/mainView.vue'
export default {
  name: 'App',
  components:{
    'mainview': mainview
  },
  data () {
    return {
      locate: {"0":"高雄市","1":"桃園市","2":"臺中市"},
      weather: [],
      locationData :[],
      nowloc : '',
      idkey : ''

    }
  },
  methods: {
    getData() {
        this.$axios.get('https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-C0032-001?Authorization=CWB-063D5A16-6216-4286-ACBB-54158755DE67&format=JSON').then(result => {
            this.weather = result.data.records.location;
            //console.log(JSON.stringify(this.weather));
            var temp = new Object();
            this.weather.forEach(function(item){
              temp[item.locationName] = new Object();
              item.weatherElement.forEach(function(witem){
                witem.time.forEach(function(titem){
                  // console.log(JSON.stringify(witem.elementName));
                  temp[item.locationName][titem.startTime + '~' + titem.endTime] = (temp[item.locationName][titem.startTime + '~' + titem.endTime]) ? temp[item.locationName][titem.startTime + '~' + titem.endTime] : new Object();
                  temp[item.locationName][titem.startTime + '~' + titem.endTime][witem.elementName] = (temp[item.locationName][titem.startTime + '~' + titem.endTime][witem.elementName]) ? temp[item.locationName][titem.startTime + '~' + titem.endTime][witem.elementName] : new Object();
                  temp[item.locationName][titem.startTime + '~' + titem.endTime][witem.elementName] = titem.parameter;
                });
              });
            });
            this.weather = temp;
            // console.log(JSON.stringify(temp));
        }) .catch(error => {
            console.log(error);
        });
    },
    setWeather(loc, id) {
      this.locationData = this.weather[loc];
      this.nowloc = loc;
      this.idkey = id;
      console.log(this.idkey);
    }
  },
    mounted() {
        this.getData();
    }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  components:{
    'mainview': mainview
  },
}
</script>


