import Vue from 'vue'
import Router from 'vue-router'
import mainView from '@/components/mainView'
import axios from 'axios'

Vue.use(Router)
Vue.prototype.$axios = axios;
export default new Router({
  routes: [
    {
      path: '/',
      name: 'mainView',
      component: mainView
    }
  ]
})



